
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    width: 100%;
    height: 100vh;
    background-color: rgb(116, 163, 204);
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(#fcf5f5, #eff4fd);
  }
  
  .main-div {
    width: 30vw;
    padding:40px;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px,
      rgba(17, 17, 26, 0.1) 0px 8px 8px;
  }
  @media only screen and (max-width: 1000px){
    .main-div {
      width: 90vw;
      padding:50px;
  }
}
  h1 {
    text-align: center;
    font-family: "Bitter", serif;
    text-transform: capitalize;
    font-size: 20px;
    letter-spacing: 5;
    word-spacing: 5px;
    margin-bottom: 50px;
    color: #2c3148;
  }
  @media only screen and (max-width: 1000px){
    h1 {
    font-size:18px;
    }
    
  }
  
  .main-heading {
    display: flex;
    margin-top: 30px;
    padding: 20px 0 20px 20px;
    background: #f1f4ff;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.16) 0 1px 4px;
  }
  @media only screen and (max-width: 1000px){
    .main-heading {
     
      margin-top: 20px;
      padding: 15px 0 10px 15px;
    }
  }
  
  .main-heading h3 {
    margin-left: 30px;
    font-family: "Mate SC", serif;
    color: #303243;
    font-family: "Raleway", sans-serif;

    letter-spacing: 1px;
    font-weight: 400;
    font-size: 15px;
  }
  @media only screen and (max-width: 400px){
    .main-heading h3 {
      margin-left: 20px;
      font-size: 10px;
      font-weight: 600;
      letter-spacing: 0.7px;
    }}

  .main-heading p {
    cursor: pointer;
    border-radius: 50%;
    box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px,
      rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px;
    background: #fff;
    padding: 7px;
  }
  .answers {
    padding: 30px 20px 40px 20px;
    font-family: "Raleway", sans-serif;
    font-size: 15px;
    word-spacing: 2px;
    font-weight: 500;
    line-height: 1.7;
    background: #fcf5f5;
    color: rgb(40, 37, 53);
  }
  @media only screen and (max-width: 1000px){
    .answers {
      font-size: 10px;
      padding: 20px 10px 30px 10px;

    }}